<template>
  <div class="bonus-operations">
    <div class="operations-cover">
      <p class="title">{{ $localizeFilter('operations_history') }}</p>
    </div>
    <MyBusinessOperationsTable :showTable="operations.length > 0" :operations="operations" :showOptions="false" />
    <EmptyTable v-if="operations.length == 0" :text="$localizeFilter('no_operations')" />
    <pagination v-model="page" :records="count" :per-page="pageSize" @paginate="pageChangeHandler" />
  </div>
</template>


<script>
import { defineComponent } from '@vue/composition-api'
import MyBusinessOperationsTable from '@/components/MyBusinessOperationsTable'
import paginationMixin from '@/mixins/pagination.mixin'
import localizeFilter from "@/filters/localize.filter"
import EmptyTable from "@/components/EmptyTable"

export default defineComponent({
  props: ['type', 'showOptions', 'coin'],
  components: {
    MyBusinessOperationsTable, EmptyTable
  },
  mixins: [paginationMixin],
  watch: {
    page() {
      this.$nextTick(() => { this.loadData() });
    },
    coin(newVal, oldVal) {
      this.$nextTick(() => { this.loadData() });
    }
  },
  setup() {

  },
  data: () => ({
    operations: [],
    count: 0,
    status: "all",
  }),
  mounted() {
    this.loadData()
  },
  methods: {
    changeStatus(status) {
      this.status = status
      this.loadData()
    },
    async loadData() {
      try {
        // // console.log(this.coin)
        const data = { skip: this.skip, limit: this.limit, currencyCode: 'CWPT' }
        this.operations = await this.$store.dispatch('getBusinessBalanceOperationsByFilter', data)

        const { count } = await this.$store.dispatch('getBusinessBalanceOperationsByFilterCount', data)
        this.count = count
        this.setupPagination(this.operations)
      } catch (e) {
        this.$error(e)
      }
    }
  }
})
</script>

