  data: () => ({
    types: ['all', 'replenish', 'dividend'],
    type: 'all'
  }),
  methods: {
    selectOption(idx) {
      this.type = this.types[idx]
      // this.$emit('changeStatus', this.type)
    },
    getClass(idx) {
      return ((idx % 2 == 0) ? 'dark' : 'light')
    },
    getTypeString(operation) {
      if (operation.amount > 0) {
        return this.$localizeFilter('income')
      } else {
        return this.$localizeFilter('expense')
      }
    },
    getAmount(x) {
      return Number(BigInt(x || '0') / BigInt(1e18))
    }
  },
}
